import React, { useContext, useEffect, useState } from "react";
import CircularProgress from "@mui/material/CircularProgress";
import AuthService from "../../services/auth.service";
import { useNavigate } from "react-router-dom";
import AlertDismissible from "../../../common/components/AlertDismissible";
import AppContext from "store/app-store";

function Activate(props) {
  const { getLang } = useContext(AppContext);
  const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
  const navigate = useNavigate();
  const urlParams = new URLSearchParams(window.location.search);
  const token = urlParams.get("token");
  const email = urlParams.get("email");
  const [message, setMessage] = useState(null);

  useEffect(async () => {
    const response = await AuthService.activate(token, email);
    if (!!!response.success && !response.success) {
    } else {
      setMessage(!!response?.body?.message && response?.body?.message);
    }
    await sleep(3000);
    navigate("/" + getLang() + "/login");
  }, []);
  return (
    <div className="row">
      <div className="col-md-8 offset-md-2">
        <div className="account-content">
          {message !== null ? (
            <AlertDismissible
              type={"success"}
              boldMsg={"Activated!"}
              msg={message}
            />
          ) : (
            <div className="centered">
              <h4>
                <div>
                  <CircularProgress />
                </div>
                <div>
                  <span style={{ color: "#15558d" }}>activating...</span>
                </div>
              </h4>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Activate;
